import { createApi } from "./ajax";
import { baseURL } from "./config";

const prefix = "wxProject";
const option = { baseURL: baseURL };

export const addWxProject = createApi(
  `${baseURL}/${prefix}/addProjects`,
  option
);

export const getProjectsList = createApi(
  `${baseURL}/${prefix}/getProjectsList`,
  option
);

export const getProjectById = createApi(
  `${baseURL}/${prefix}/getProjectById`,
  option
);

export const addFrontEndShareId = createApi(
  `${baseURL}/${prefix}/addFrontEndShareId`,
  option
);

export const getFrontEndShareIdInfo = createApi(
  `${baseURL}/${prefix}/getFrontEndShareIdInfo`,
  option
);

export const helpFrontEndShareId = createApi(
  `${baseURL}/${prefix}/helpFrontEndShareId`,
  option
);
