<template>
  <div @click="handleClick(info)" class="info-item">
    <div>{{ info.title }}</div>
    <div>{{ info.desc }}-{{ info.topStatus }}</div>
    <div>{{ date }}</div>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted, computed } from "vue";
export default {
  name: "info-item",
  components: {},
  props: {
    info: Object,
  },
  setup(props, ctx) {
    const state = reactive({
      infoList: [],
    });

    onMounted(() => {
      init();
    });

    const date = computed(() => {
      let num = props.info.updateDate;
      let year = new Date(num).getFullYear();
      let mon = new Date(num).getMonth() + 1;
      let day = new Date(num).getDate();
      let hour = new Date(num).getHours();
      let min = new Date(num).getMinutes();
      let sec = new Date(num).getSeconds();
      return `${year}.${mon}.${day} ${hour}:${min}:${sec}`;
    });
    const handleClick = (info) => {
      ctx.emit("itemClick", info.id);
    };

    const init = async () => {
      console.log(state);
    };

    return {
      ...toRefs(state),
      date,
      handleClick,
    };
  },
};
</script>
