<template>
  <div class="index">
    <infoItem
      v-for="(info, index) in infoList"
      :key="'infoList' + index"
      :info="info"
      @itemClick="handleItemClick"
    />
    <el-button @click="handleHelp">帮助</el-button>
    <el-button @click="handlePublish">发布</el-button>
    <el-button @click="handleShare">分享</el-button>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted } from "vue";
import { useRouter } from "vue-router";
import infoItem from "../components/infoItem.vue";
import { getProjectsList } from "../api/wxProject.js";
export default {
  name: "index",
  components: { infoItem },
  setup() {
    let router = useRouter();
    const state = reactive({
      infoList: [],
    });

    onMounted(() => {
      init();
    });

    const init = async () => {
      console.log("initaaa");
      getProjectsList(
        {},
        (res) => {
          // console.log(res);
        },
        (res) => {
          state.infoList = res;
        },
        {
          method: "get",
        }
      );
    };
    const methods = {
      handleItemClick: (id) => {
        router.push({
          path: "/indexDetail",
          query: {
            id,
          },
        });
      },
      handlePublish() {
        router.push({
          path: "/uploadPage",
        });
      },
      handleShare() {
        router.push({
          path: "/sharePage",
        });
      },
      handleHelp() {},
    };

    return {
      ...toRefs(state),
      ...methods,
    };
  },
};
</script>
