import { createRouter, createWebHashHistory } from "vue-router";
import Index from "../pages/index.vue";

const routes = [
  {
    path: "/",
    name: "index",
    component: Index,
  },
  {
    path: "/indexDetail",
    name: "index-detail",
    component: () => import("../pages/indexDetail.vue"),
  },
  {
    path: "/uploadPage",
    name: "upload-Page",
    component: () => import("../pages/upload.vue"),
  },
  {
    path: "/sharePage",
    name: "share-Page",
    component: () => import("../pages/share.vue"),
  },
  {
    path: "/frontEndSharePage",
    name: "front-end-share-Page",
    component: () => import("../pages/frontEndShare.vue"),
  },
  {
    path: "/frontEndHelpPage",
    name: "front-end-help-Page",
    component: () => import("../pages/frontEndHelp.vue"),
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import("../pages/About.vue"),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  // history: createWebHistory(),
  routes,
});

export default router;
